import React, { Component } from "react";

class BlockHead extends Component {
	render() {
		return (
			<div className='headBlk'>
				<div className='miniDv'>
					<strong>Sort:</strong>
					<select
						name=''
						id=''
						className='txtBox'
					>
						<option value='0'>
							All
						</option>
						<option value='0'>
							1
						</option>
						<option value='0'>
							2
						</option>
						<option value='0'>
							3
						</option>
						<option value='0'>
							4
						</option>
						<option value='0'>
							5
						</option>
					</select>
				</div>
				<div className='miniDv'>
					<strong>Filter:</strong>
					<select
						name=''
						id=''
						className='txtBox'
					>
						<option value='0'>
							All
						</option>
						<option value='0'>
							1
						</option>
						<option value='0'>
							2
						</option>
						<option value='0'>
							3
						</option>
						<option value='0'>
							4
						</option>
						<option value='0'>
							5
						</option>
					</select>
				</div>
				<div className='miniDv'>
					<input
						type='text'
						name=''
						id=''
						className='txtBox'
						placeholder='Search'
					/>
				</div>
				<div className='miniDv viewList'>
					<strong>View:</strong>
					<div
						id='listView'
						className='active'
						onClick={
							this
								.listView
						}
					></div>
					<div
						id='gridView'
						onClick={
							this.props
								.switchView
						}
					></div>
					<div
						id='cardView'
						onClick={
							this.props
								.cardView
						}
					></div>
				</div>
				<div className='miniDv actionDv'>
					<strong>Actions:</strong>
					<ul className='miniLst flex semi'>
						<li>
							<button type='button'>
								Remove
							</button>
						</li>
						<li>
							<button type='button'>
								Select
								All
							</button>
						</li>
						<li>
							<button type='button'>
								Deselect
								All
							</button>
						</li>
					</ul>
				</div>
			</div>
		);
	}
}

export default BlockHead;
